@import "https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap";
:root {
  --ec-primary-color-50: #f4f9f8;
  --ec-primary-color-100: #dbece7;
  --ec-primary-color-200: #b3d6cd;
  --ec-primary-color-300: #8bbdb2;
  --ec-primary-color-400: #639e93;
  --ec-primary-color-500: #498379;
  --ec-primary-color-600: #386961;
  --ec-primary-color-700: #305550;
  --ec-primary-color-800: #2a4541;
  --ec-primary-color-900: #263b39;
  --ec-primary-color-950: #122120;
  --ec-accent-color-50: #e9f4f0;
  --ec-accent-color-100: #d9eee6;
  --ec-accent-color-200: #b3dccf;
  --ec-accent-color-300: #85c3b1;
  --ec-accent-color-400: #5ca592;
  --ec-accent-color-500: #428a78;
  --ec-accent-color-600: #336e61;
  --ec-accent-color-700: #2c594f;
  --ec-accent-color-800: #274842;
  --ec-accent-color-900: #243d39;
  --ec-accent-color-950: #10231f;
  --ec-primary-color: var(--ec-primary-color-200);
  --ec-primary-color-hover: var(--ec-primary-color-300);
  --ec-accent-color: var(--ec-accent-color-50);
  --ec-accent-color-hover: var(--ec-accent-color-100);
  --ec-heading-font: "Crake", serif;
  --ec-body-font: "Inter", sans-serif;
  --ec-font-color: #3e4849;
  --ec-line-height: 1.5;
  --ec-font-weight: 400;
  --ec-font-size: 16px;
  --ec-border-color: #0000001a;
  --ec-border-radius-xs: 4px;
  --ec-border-radius-sm: 8px;
  --ec-border-radius-md: 16px;
  --ec-border-radius-lg: 20px;
  --ec-border-radius-xl: 24px;
  --ec-spacing-xxs: 4px;
  --ec-spacing-xs: 8px;
  --ec-spacing-sm: 16px;
  --ec-spacing-md: 24px;
  --ec-spacing-lg: 32px;
  --ec-spacing-xl: 40px;
  --ec-spacing-xxl: 48px;
  --ec-box-shadow: 0px 4px 8px 0px #0000000d;
  --ec-transition: all .3s ease-in-out;
  --ec-transition-fast: all .1s ease-in-out;
  --ec-transition-slow: all .5s ease-in-out;
  --ec-transition-cubic: all .3s cubic-bezier(.25, .8, .25, 1);
  --ec-transition-cubic-fast: all .1s cubic-bezier(.25, .8, .25, 1);
  --ec-transition-cubic-slow: all .5s cubic-bezier(.25, .8, .25, 1);
  --ec-transition-linear: all .3s linear;
  --ec-transition-linear-fast: all .1s linear;
  --ec-transition-linear-slow: all .5s linear;
  --ec-transition-ease-in: all .3s ease-in;
  --ec-transition-ease-in-fast: all .1s ease-in;
  --ec-transition-ease-in-slow: all .5s ease-in;
  --ec-transition-ease-out: all .3s ease-out;
  --ec-transition-ease-out-fast: all .1s ease-out;
  --ec-transition-ease-out-slow: all .5s ease-out;
  --ec-transition-ease-in-out: all .3s ease-in-out;
  --ec-transition-ease-in-out-fast: all .1s ease-in-out;
  --ec-transition-ease-in-out-slow: all .5s ease-in-out;
  --ec-transition-none: none;
}

html, body {
  font-family: var(--ec-body-font);
  line-height: var(--ec-line-height);
  font-weight: var(--ec-font-weight);
  font-size: var(--ec-font-size);
  color: var(--ec-font-color);
  font-synthesis: none;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
}

.container {
  max-width: 1128px;
  margin: 0 auto;
}

@media screen and (width <= 510px) {
  :root {
    --product-list-grid-gap: var(--ec-spacing-xs, 8px);
    --product-list-grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }
}

@media screen and (width >= 768px) {
  :root {
    --order-lines-item-image-width: 128px;
    --order-lines-item-image-height: 128px;
    --order-lines-remove-position: absolute;
    --order-lines-remove-top: 0;
    --order-lines-remove-right: 0;
    --order-lines-item-content-text-wrapper-margin: 0 40px 0 0;
    --cart-price-flex: 0 0 30%;
    --cart-price-align-self: flex-start;
    --order-header-flex-direction: row;
    --order-header-justify-content: flex-start;
    --order-header-gap: 48px;
    --order-header-item-flex-direction: column;
    --order-header-item-width: auto;
    --product-detail-brand-margin-bottom: 8px;
    --product-detail-title-font-size: 50px;
    --product-detail-price-font-size: 26px;
    --product-list-item-padding: var(--ec-spacing-md, 24px);
    --search-field-background-color-overlay-background: #0003;
    --login-dialog-width: 630px;
    --login-dialog-height: auto;
    --login-dialog-max-height: 85vh;
    --dialog-content-border-radius: var(--ec-border-radius-lg, 20px);
    --close-button-top: 16px;
    --close-button-right: 16px;
  }
}

@media screen and (width >= 1080px) {
  :root {
    --product-detail-image-slider-border: 1px solid #0000001a;
    --product-detail-image-slider-border-radius: var(--ec-border-radius-lg, 20px);
  }
}

